<template>
    <div class="xui-vue markdown-section">
        <div class="main">
            <h1>message</h1>
            <blockquote>
                <p>: ,<code>message</code>,</p>
            </blockquote>
            <h5 id="">:</h5>
            <codes :codes="params1"></codes>
            <codes :codes="params"></codes>
            <ul>
              <li><code>string</code> <strong></strong></li>
              <li><code>time</code>,1000ms</li>
              <li><code>callback</code></li>
              <li>? : <code>xumeng0611@gmail.com</code></li>
            </ul>
            <h3></h3>
            <p>1.,1s.</p>
            <xui-button type="default" @click="open1"></xui-button>
            <codes :codes="open_1"></codes>
            <p>2.,</p>
            <xui-button type="default" @click="open2"></xui-button>
            <codes :codes="open_2"></codes>
            <p>3.,,~</p>
            <xui-button type="default" @click="open3"></xui-button>
            <codes :codes="open_3"></codes>
        </div>
    </div>
</template>

<script>
import Codes from '@/assets/codes.vue';

export default {
  name: 'show',
  data () {
	return {
    params: 'this.$message(string, time, callback);',
    params1: `import Message from './plugins/message/message.js';
Vue.use(Message);`,
    open_1: 'this.$message("hello,world")',
    open_2: 'this.$message("hello,world", 2000)',
    open_3: 
`this.$message(
    'hello, world', 
    1500, 
    function(){
    console.log('callback');
}`
	}
  },
  mounted(){
      if ('ontouchstart' in window) {
          let tar = document.getElementById('app');
          tar.querySelector('.test').classList.add('folded');
          tar.querySelector('.main').classList.add('folded');
      }
  },
  methods: {
    open1(){
      this.$message('hello,world');
    },
    open2(){
      this.$message('hello,world', 2000);
    },
    open3(){
      let that = this;
        that.$message(
          'hello, world', 
          1500, 
          function(){
          that.$message('callback');
        }
      );
    }
  },
  components:{
    Codes
  }
}
</script>